import React from 'react';
import { useMeals } from '../../../contexts/MealsContext';
import { Button, Spinner } from 'react-bootstrap';

const LeaveMealButton = ({ mealId, onSuccess }) => {
  const { leaveMeal, loading } = useMeals();

  const handleLeave = async () => {
          if (window.confirm('Sei sicuro di voler abbandonare questo TableTalk¬Æ?')) {
      try {
        const updatedMeal = await leaveMeal(mealId); // Questa funzione ora restituisce il TableTalk¬Æ aggiornato
        if (onSuccess) {
          onSuccess(updatedMeal); // Passiamo l'INTERO TableTalk¬Æ aggiornato al genitore
        }
      } catch (error) {
        // Il toast di errore √® gi√† gestito dal context
        console.error("Fallimento leaveMeal nel componente:", error);
      }
    }
  };

  return (
    <Button variant="outline-danger" className="w-100" onClick={handleLeave} disabled={loading}>
      {loading ? (
        <>
          <Spinner as="span" animation="border" size="sm" />
          <span> Uscita in corso...</span>
        </>
      ) : (
        'Abbandona il TableTalk¬Æ'
      )}
    </Button>
  );
};

export default LeaveMealButton;